<template>
    <div class="health">
        <!--       -->
        <el-breadcrumb separator="/" style="padding-left:10px;padding-bottom:10px;font-size:12px;">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--        style="height: 590px;"-->
        <el-card style="height: 590px;">
            <el-alert
                    :closable="false"
                    style="margin-bottom: 20px;"
                    v-if="reported"
                    show-icon
                    :title="',,,,:['+ruleForm.createTime+']'"
                    type="success">
            </el-alert>
            <h5 style="margin: 0px;padding: 0px;">  <div style="float: right;margin-bottom: 10px;vertical-align: center;">
            <el-button @click="OpenHistory"   size="small" type="warning"></el-button>
                <el-button style="margin-top: 5px;" type="primary"   size="small" @click="onSubmit" v-if="!reported"></el-button>
                <el-button @click="reset"  size="small" v-if="!reported"></el-button>
            </div></h5>

            <!--            -->
            <el-drawer
                    :with-header="false"
                    :visible.sync="drawer"
                    :direction="direction"
                    >
                <span>
                    <el-card class="box-card"  v-for="item in this.historyTable" :key="item.id" style="margin-bottom: 10px;">
                      <div class="text item">
                         <ul>
                             <li>:{{userInfo.username}}</li>
                             <li>:{{item.address}}</li>
                             <li>:{{item.createTime}}</li>
                             <li>:
                                 <div style="float: right">
                                       <el-tag v-if="item.situation===0" size="small" effect="dark" type="success"></el-tag>
                                <el-tag v-else-if="item.situation===1" size="small" effect="dark" type="danger"></el-tag>
                                <el-tag v-else-if="item.situation===2"  size="small" effect="dark"  type="warning"></el-tag>
                                 </div>
                             </li>
                         </ul>
                      </div>
<!--                        -->

                    </el-card>
                     <el-pagination
                             @current-change="handleCurrentChange"
                             :current-page="queryMap.pageNo"
                             :page-size="queryMap.pageSize"
                             style="margin-top: 20px;"
                             background
                             small
                             layout="prev, pager, next"
                             :total="historyTotal">
                        </el-pagination>
                </span>
            </el-drawer>

            <el-divider></el-divider>
            <el-form size="small" label-width="80px">
                <el-row :gutter="20">
                    <el-col :span="8">
                        <div class="grid-content bg-purple">
                            <el-form-item label="">
                                <el-input v-model="userInfo.username" disabled></el-input>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div class="grid-content bg-purple">
                            <div class="grid-content bg-purple">
                                <el-form-item label="">
                                    <el-date-picker
                                            disabled
                                            v-model="nowTime"
                                            type="datetime"
                                            placeholder="">
                                    </el-date-picker>

                                </el-form-item>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div class="grid-content bg-purple">
                            <div class="grid-content bg-purple">
                                <el-form-item label="">
                                    <el-input v-model="userInfo.department" disabled></el-input>
                                </el-form-item>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-form>

            <h5 style="margin: 0px;padding: 0px;"></h5>

            <el-divider></el-divider>
            <el-form  size="small" :inline="true" :label-position="'top'" :model="ruleForm" :rules="rules" ref="ruleFormRef" label-width="100px" class="demo-ruleForm">
                <el-row :gutter="20">
                    <el-col :span="6">
                        <div class="grid-content bg-purple">
                            <el-form-item label="" prop="valueProvince">
                                <el-select
                                        :disabled="reported"
                                        v-model="ruleForm.valueProvince"
                                        placeholder=""
                                        @change="changeProvince"
                                >
                                    <el-option
                                            v-for="item in provinceList"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="grid-content bg-purple">
                        <el-form-item label="" prop="valueCity">
                            <el-select
                                    :disabled="reported"
                                    v-model="ruleForm.valueCity"
                                    placeholder=""
                                    @change="changeCity"
                            >
                                <el-option
                                        v-for="item in cityOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="grid-content bg-purple">
                            <el-form-item label="" prop="valueOrigin">
                                <el-select
                                        :disabled="reported"
                                        v-model="ruleForm.valueOrigin"
                                        placeholder=""
                                        @change="changeOrigin"
                                >
                                    <el-option
                                            v-for="item in originOptions"
                                            :key="item.label"
                                            :label="item.label"
                                            :value="item.value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="grid-content bg-purple">
                    <el-form-item v-if="reported" label="" prop="valueOrigin">
                        <el-tag size="small">
                            {{ruleForm.address}}
                        </el-tag>
                    </el-form-item>
                        </div>
                    </el-col>
                </el-row>

                <el-row :gutter="20">
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-form-item label="" prop="situation">
                                <el-radio-group :disabled="reported" v-model="ruleForm.situation">
                                    <el-radio :label="0"></el-radio>
                                    <el-radio :label="1"></el-radio>
                                    <el-radio :label="2"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-form-item label="?  ()" prop="touch">
                                <el-radio-group :disabled="reported" v-model="ruleForm.touch">
                                    <el-radio :label="1"></el-radio>
                                    <el-radio :label="0"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-form-item label="202011, ()" prop="passby">
                                <el-radio-group :disabled="reported" v-model="ruleForm.passby">
                                    <el-radio :label="1"></el-radio>
                                    <el-radio :label="0"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-form-item label="202011,,)" prop="reception">
                                <el-radio-group :disabled="reported" v-model="ruleForm.reception">
                                    <el-radio :label="1"></el-radio>
                                    <el-radio :label="0"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </el-col>
                </el-row>

            </el-form>

        </el-card>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                drawer: false,
                direction: 'rtl',
                queryMap:{pageSize:4,pageNum:1},
                reported:false,
                historyTable:[],
                historyTotal:0,
                provinceList: [], // 
                cityList: [], // 
                originList: [], // 
                cityOptions: [], // 
                originOptions: [] // 
                ,
                ruleForm: {},
                nowTime: new Date(),
                userInfo: {},
                rules: {
                    situation: [
                        {required: true, message: "", trigger: "blur"},
                    ],
                    touch: [
                        {required: true, message: "", trigger: "blur"},
                    ],
                    passby: [
                        {required: true, message: "", trigger: "blur"},
                    ],
                    reception: [
                        {required: true, message: "", trigger: "blur"},
                    ],
                    valueProvince: [
                        { required: true, message: "", trigger: "blur" }
                    ],
                    valueCity: [{ required: true, message: "", trigger: "blur" }],
                    valueOrigin: [
                        { required: true, message: "", trigger: "blur" }
                    ],
                },
            }
        },
        methods: {
            OpenHistory(){
                this.drawer = true;
                this.reportHistory();
            },
            /**
             * 
             */
            handleCurrentChange(current) {
                this.queryMap.pageNum = current;
                this.reportHistory();
            },

            //
            async reportHistory(){
                const { data: res } = await this.$http.get("business/health/history",{
                    params: this.queryMap
                });
                if(!res.success){
                    return this.$message.error(":" + res.msg);
                }else {
                    this.historyTable=res.data.rows;
                    this.historyTotal=res.data.total;
                }
            },
            //
            async isReport(){
                const { data: res } = await this.$http.get("business/health/isReport");
                if(!res.success){
                    return this.$message.error(":" + res.msg);
                }else {
                    if(res.data){
                        this.reported=true;
                        this.ruleForm=res.data;
                    }else {
                        this.reported=false;
                        this.$notify.warning({
                            title: '',
                            message: ',~'
                        });
                    }
                }
            },
            //
            onSubmit(){
                this.$refs.ruleFormRef.validate(async valid => {
                    if (!valid) {
                        return;
                    } else {
                        this.ruleForm.address =
                            this.ruleForm.province +
                            "/" +
                            this.ruleForm.city +
                            "/" +
                            this.ruleForm.origin;
                        const { data: res } = await this.$http.post(
                            "business/health/report",
                            this.ruleForm
                        );
                        if (res.success) {
                            this.reported=true;
                            this.ruleForm.createTime=this.nowTime;
                            this.$message.success("");
                        } else {
                            return this.$message.error(":" + res.msg);
                        }
                    }
                });
            },

            //
            reset(){
                this.$refs.ruleFormRef.clearValidate();
                this.ruleForm={};
            },
            currentTime() {
                setInterval(this.getDate, 500);
            },
            getDate() {
                this.nowTime = new Date();

            },
            // 
            changeProvince(val) {
                this.provinceList.forEach((province, index) => {
                    if (val.toString() === this.provinceList[index].value) {
                        this.cityOptions = this.provinceList[index].children;
                        this.ruleForm.valueCity = this.provinceList[
                            index
                            ].children[0].value; //
                        this.ruleForm.city = this.provinceList[index].children[0].label;

                        this.ruleForm.valueOrigin = this.provinceList[
                            index
                            ].children[0].children[0].value; //
                        this.ruleForm.origin = this.provinceList[
                            index
                            ].children[0].children[0].label;

                        this.originOptions = this.provinceList[index].children[0].children;
                        //set value
                        this.ruleForm.province = this.provinceList[index].label;
                    }
                });
            },
            // 
            changeCity(val) {
                this.cityList.forEach((city, index) => {
                    if (val.toString() === this.cityList[index].value) {
                        this.originOptions = this.cityList[index].children;
                        this.ruleForm.valueOrigin = this.cityList[index].children[0].value; //;
                        //set value
                        this.ruleForm.city = this.cityList[index].label;
                    }
                });
            },
            // 
            changeOrigin(val) {
                this.ruleForm.valueOrigin = val;

                this.originList.forEach((origin, index) => {
                    if (val.toString() === this.originList[index].value) {
                        //set value
                        this.ruleForm.origin = this.originList[index].label;
                    }
                });
                //this.$forceUpdate();render
                this.$forceUpdate();
            },

            _getJsonData() {
                this.$http.get("/json/provinces.json").then(res => {
                    this.provinceList = [];
                    this.cityList = [];
                    this.originList = [];
                    res.data.forEach(item => {
                        if (item.value.match(/0000$/)) {
                            this.provinceList.push({
                                value: item.value,
                                label: item.name,
                                children: []
                            });
                        } else if (item.value.match(/00$/)) {
                            this.cityList.push({
                                value: item.value,
                                label: item.name,
                                children: []
                            });
                        } else {
                            this.originList.push({
                                value: item.value,
                                label: item.name
                            });
                        }
                    });
                    for (let index in this.provinceList) {
                        for (let index1 in this.cityList) {
                            if (
                                this.provinceList[index].value.slice(0, 2) ===
                                this.cityList[index1].value.slice(0, 2)
                            ) {
                                this.provinceList[index].children.push(this.cityList[index1]);
                            }
                        }
                    }
                    for (let item1 in this.cityList) {
                        for (let item2 in this.originList) {
                            if (
                                this.originList[item2].value.slice(0, 4) ===
                                this.cityList[item1].value.slice(0, 4)
                            ) {
                                this.cityList[item1].children.push(this.originList[item2]);
                            }
                        }
                    }
                });
            }
        },
        created() {
            this.isReport();
            this.userInfo = this.$store.state.userInfo;
            this._getJsonData();
        },
        mounted() {
            this.currentTime();
        },
        // 
        beforeDestroy: function () {
            if (this.getDate) {
                clearInterval(this.getDate); // Vue
            }
        }

    }
</script>
